<template>
  <div class="singer">
    <div class="type">
      <van-button
        round
        hairline
        type="primary"
        v-for="item in areaList"
        :key="item.id"
        :class="areaId == item.id ? 'active area-but' : 'area-but'"
        @click="chooseArea(item.id)"
        >{{ item.name }}</van-button
      >
    </div>
    <div class="type genre">
      <van-button
        round
        hairline
        type="primary"
        v-for="item in sexList"
        :key="item.id"
        :class="sexId == item.id ? 'active area-but' : 'area-but'"
        @click="chooseSex(item.id)"
        >{{ item.name }}</van-button
      >
      <van-collapse v-model="activeNames" class="genreWrapper" >
        <van-collapse-item :title="genre" name="1" ref="genreRef">
          <van-list>
            <van-cell
              v-for="item in genreList"
              :key="item.id"
              class="genreCell"
              style="display: block"
              @click="chooseGenre(item)"
              >{{ item.name }}<van-divider
            /></van-cell>
          </van-list>
        </van-collapse-item>
      </van-collapse>
    </div>
    <div class="type">
      <van-button
        round
        hairline
        type="primary"
        v-for="item in indexList"
        :key="item.id"
        :class="indexId == item.id ? 'active area-but' : 'area-but'"
        @click="chooseIndex(item.id)"
        >{{ item.name }}</van-button
      >
    </div>
    <div class="singerList">
      <div class="singerItem" v-for="item in singerList" :key="item.singer_mid" @click="toSingerDetail(item.singer_mid)"> 
        <img  v-if="item.singer_pic.length" :src="item.singer_pic">
        <span>{{item.singer_name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { onMounted, reactive, toRefs, ref } from "vue";
import { useRouter } from 'vue-router';
export default {
  setup() {
    const router = useRouter()
    const state = reactive({
      areaList: [],
      sexList: [],
      genreList: [],
      indexList: [],
      singerList:[],
      areaId: -100,
      sexId: -100,
      genreId: -100,
      indexId: -100,
      genre: "全部",
      finished: true,
    });
    const activeNames = ref(["-1"]);
    const genreRef = ref(null);
    onMounted(() => {
      searchSinger()
    });
    const chooseArea = (id) => {
      state.areaId = id;
      searchSinger()
    };
    const chooseSex = (id) => {
      state.sexId = id;
      searchSinger()
    };
    const chooseGenre = (item) => {
      state.genre = item.name;
      state.genreId = item.id;
      genreRef.value.toggle()
      searchSinger()
    };
    const chooseIndex = (id) => {
      state.indexId = id;
      searchSinger()
    };
    const searchSinger = ()=>{
      axios({
        url:`/api/getSingerList?area=${state.areaId}&sex=${state.sexId}&index=${state.indexId}&genre=${state.genreId}`
      }).then(res=>{
        console.log(res.data.response.singerList.data);
        state.areaList = res.data.response.singerList.data.tags.area;
        state.sexList = res.data.response.singerList.data.tags.sex;
        state.genreList = res.data.response.singerList.data.tags.genre;
        state.indexList = res.data.response.singerList.data.tags.index;
        state.singerList = res.data.response.singerList.data.singerlist
      })
    }
    const toSingerDetail = (id)=>{
      console.log(id);
      router.push({path:'/singerDeatil',query:{id:id}})
    }
    return {
      chooseArea,
      chooseSex,
      chooseGenre,
      chooseIndex,
      activeNames,
      genreRef,
      searchSinger,
      toSingerDetail,
      ...toRefs(state),
    };
  },
};
</script>

<style lang="less" scoped>
div::-webkit-scrollbar {
  display: none;
}
.singer {
  .type {
    margin-top: 20px;
    margin-left: -5px;
    display: flex;
    flex-wrap: wrap;
    .area-but {
      background-color: #fff;
      color: #111;
      border: none;
      height: 30px;
      line-height: 30px;
      margin: 7px;
      width: 74px;
      text-align: center;
    }
    .area-but:not(.active):hover {
      color: #1ece9a;
    }
    .active {
      background-color: #1ece9a;
      color: #fff;
    }
  }
  .genre {
    position: relative;
    .genreWrapper {
      position: absolute;
      right: 0;
      width: 90px;
      cursor: pointer;
      .van-list {
        height: 314px;
        overflow-y: scroll;
      }

      .genreCell:hover {
        cursor: pointer;
        color: #1ece9a;
      }
    }
  }
  .singerList{
    display: flex;
    flex-wrap: wrap;
    .singerItem{
      width: 20%;
      margin: 20px;
      text-align: center;
      img{
        width: 100%;
        border-radius: 50%;
      }
    }
    .singerItem:hover{
      color: #1ece9a;
      cursor: pointer;
    }
  }
}
</style>